@mixin scroll {
	table {
		--background-color: #fff;
		// &::before, &::after {
		// 	content: '';
		// 	display: block;
		// 	width: 2rem;
		// 	height: 100vh;
		// 	position: sticky;
		// 	top: 0;
		// 	background-color: rgba(#000, 0.5);
		// }
		// &::before {
		// 	margin-bottom: -100vh;
		// 	left: 0;
		// }
		// &::after {
		// 	margin-top: -100vh;
		// 	right: 0;
		// }


		position: relative;
		margin: { left : auto; right: auto; }

		// background-size: 1.25rem 100%, 1.25rem 100%;
		// background-attachment: scroll, scroll;
		// background-repeat: no-repeat;
		// background-position-x: 0, right;

		transition: box-shadow 1s;
		&.left-shade {
			box-shadow: inset 1rem 0 1rem rgba(#000, 0.1);
		}
		&.right-shade {
			box-shadow: inset -1rem 0 1rem rgba(#000, 0.1);
		}
		&.left-shade.right-shade {
			box-shadow: inset 1rem 0 1rem rgba(#000, 0.1), inset -1rem 0 1rem rgba(#000, 0.1);
		}

		caption {
			display: block;
			position: sticky;
			left: 0;
			right: 0;
			background-color: var(--background-color);

			> span {
				display: inline-block;
				text-align: left;
			}
		}
		td, th {
			position: relative;
		}
		th {
			background-color: lightblue;
			z-index: -1;
		}
	}
	@include stile-table-pseudo-scroll-bar;
}

@mixin stile-table-pseudo-scroll-bar {
	div[data-stile ~= 'fixed-table-scroll-bar'] {
		position  : fixed;
		bottom    : 0;
		z-index   : 99;
		margin    : 0;
		overflow-x: auto;
		overflow-y: hidden;  // for IE11 & Edge

		height: calc(1rem + 2px);  // for Safari
		border: 1px solid rgba(transparent, 0.1);  // for Safari

		display: none;
		@media print { display: none !important; }

		> div {  // Spacer
			height: 1px;
		}
	}
}
